  .collection__hdr {
    display: flex;
    justify-content: center;
    padding: 1.25em 0 0;
  }
}

.collection__hdr .lib-icon--brush {
  font-size: 2.2px;
  position: absolute;
  right: -0.75rem;
  top: 2.25rem;
}

.collection__hdr .lib-icon--brush::before {
  background-position: 0 -31em;
}

@media only screen and (min-width: 768px) {
  .collection__hdr .lib-icon--brush {
    position: relative;
    right: 0.75rem;
    top: 1.875rem;
  }
}

.collection__hdr .icon--3-stars-reverse {
  font-size: 4px;
  left: -0.1875rem;
  position: absolute;
  top: 0.625rem;
}

@media only screen and (min-width: 768px) {
  .collection__hdr .icon--3-stars-reverse {
    font-size: 5px;
    left: -0.4375rem;
    position: relative;
    top: -0.5rem;
  }
}

.collection__hdr h2 {
  font-family: "FreightTextBook", serif;
  font-size: 2.625rem;
}

